import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import {
  CookieWarn, CookieWarnClose, CookieWarnText, PageWrap,
} from 'src/components/Page/styled';
import { pagesSelector } from 'src/services/pages/selectors';
import { fetchAndStorePageConfig } from 'src/services/pages/thunks';
import { setCurrentPage } from 'src/services/pages/actions';
import Buttons from 'src/components/Buttons';
import PageContent from 'src/components/PageContent';
import { cookiesWarnSelector } from 'src/services/options/selectors';
import Cookie from 'src/utils/Cookie';


class Page extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      showCookieWarn: false,
    };

    this.wrapRef = React.createRef();
    this.cookieRef = React.createRef();
  }

  componentDidMount() {
    this.props.handlePageChange();

    // Adding dynamic margin-top, because the header is fixed
    const wrapNode = this.wrapRef.current;
    wrapNode.style.marginTop = `${document.getElementById('header').clientHeight}px`;
    //
    if (window.innerWidth > 1024 && this.props.pageName === 'Location') {
      wrapNode.style.height = `${this.calcPageHeight()}px`;
    }

    if (!Cookie.isCookieApproved()) {
      this.setState(
        () => ({ showCookieWarn: true }),
        () => setTimeout(() => this.cookieRef.current.classList.add('visible'), 1500),
      );
    }
  }

  componentDidUpdate(prevProps) {
    if (prevProps.pageName !== this.props.pageName) {
      this.props.handlePageChange();

      if (window.innerWidth > 1024 && this.props.pageName === 'Location') {
        this.wrapRef.current.style.height = `${this.calcPageHeight()}px`;
      } else {
        this.wrapRef.current.style.height = 'auto';
      }
    }
  }

  hideCookieWarn = () => {
    this.cookieRef.current.classList.remove('visible');
    this.setState(() => ({ showCookieWarn: false }));
    Cookie.approveCookie();
  };

  calcPageHeight = () => window.innerHeight
    - document.getElementById('header').clientHeight
    - document.getElementById('services').clientHeight
    // additional services padding
    - 20
    // margin-top
    - 90;

  renderCookieWarn = () => (
    <CookieWarn ref={this.cookieRef}>
      <CookieWarnText>{this.props.cookieWarnText}</CookieWarnText>
      <CookieWarnClose onClick={this.hideCookieWarn} />
    </CookieWarn>
  );

  render() {
    const { showCookieWarn } = this.state;
    const { pageConfig, pageName } = this.props;
    const {
      content, form, map, buttons,
    } = pageConfig;
    const isContentPage = content || form || map;
    const isButtonsPage = !isContentPage;

    return (
      <PageWrap pageName={pageName} ref={this.wrapRef}>
        {isContentPage && <PageContent pageName={pageName} pageConfig={pageConfig} />}
        {(isContentPage && !!buttons.length) && <Buttons className="visible-desktop-up" />}

        {isButtonsPage && <Buttons />}
        {showCookieWarn && this.renderCookieWarn()}
      </PageWrap>
    );
  }
}

Page.propTypes = {
  pageName: PropTypes.string.isRequired,
  pageConfig: PropTypes.shape({
    buttons: PropTypes.array,
    content: PropTypes.string,
    form: PropTypes.object,
    map: PropTypes.bool,
  }),
  handlePageChange: PropTypes.func.isRequired,
  cookieWarnText: PropTypes.string.isRequired,
};

Page.defaultProps = {
  pageConfig: {
    buttons: [],
  },
};

const mapStateToProps = (state, ownProps) => ({
  pageConfig: pagesSelector(state)[ownProps.pageName],
  cookieWarnText: cookiesWarnSelector(state),
});

const mapDispatchToProps = (dispatch, ownProps) => ({
  handlePageChange: () => {
    dispatch(fetchAndStorePageConfig(ownProps.pageName));
    dispatch(setCurrentPage(ownProps.pageName));

    document.documentElement.scrollTop = 0;
  },
});


export default connect(mapStateToProps, mapDispatchToProps)(Page);
