import React, { Component } from 'react';
import {
  LocationInfo, LocationInfoClose,
  LocationInfoDesc,
  LocationInfoTitle,
  MapWrap,
} from 'src/components/Map/styled';
import { connect } from 'react-redux';
import { officeLocationSelector } from 'src/services/options/selectors';
import PropTypes from 'prop-types';
import { initMap } from 'src/services/location';


class Map extends Component {
  constructor(props) {
    super(props);

    this.state = { locationInfo: null };

    this.mapRef = React.createRef();
  }

  componentDidMount() {
    if (document.getElementById('map-api')) {
      this.initMap();
    } else {
      this.loadMapApi()
        .then(this.initMap);
    }

    if (window.innerWidth < 768) this.mapRef.current.style.height = `${this.calcMapHeight()}px`;
  }

  /**
   * Calculates dynamic height for the map - let footer contacts be visible on the first screen
   * @returns {number}
   */
  calcMapHeight = () => window.innerHeight
    - document.getElementById('header').clientHeight
    - document.getElementById('page-title').clientHeight
    - document.getElementById('footer-phone').clientHeight;

  loadMapApi = () => new Promise((resolve) => {
    const script = document.createElement('script');

    script.onload = resolve;
    script.async = true;
    script.defer = true;
    script.id = 'map-api';
    script.src = `https://maps.googleapis.com/maps/api/js?key=${GOOGLE_MAP_API_KEY}`;

    document.body.appendChild(script);
  });

  /**
   * @param {Object} info
   * @param {String} info.location
   * @param {String} info.description
   */
  handleMarkerClick = (info, eventTarget) => {
    const { top: markerTop, left: markerLeft } = eventTarget.getBoundingClientRect();

    const top = markerTop - this.mapRef.current.getBoundingClientRect().top;
    let left = markerLeft + 50;

    // 170 - popup width
    if ((left + 170 + 5) > window.innerWidth) {
      left = markerLeft - 170 - 40;
    }

    this.setState(() => ({
      locationInfo: {
        ...info,
        top,
        left,
      },
    }));
  };

  clearLocationInfo = () => this.setState(() => ({ locationInfo: null }));

  initMap = () => {
    const { officeLocation } = this.props;
    initMap(this.mapRef.current, officeLocation, this.handleMarkerClick);
  };

  renderLocationInfo = info => (
    <LocationInfo top={info.top} left={info.left} id="location-info">
      <LocationInfoClose onClick={this.clearLocationInfo} />
      <LocationInfoTitle>{info.location}</LocationInfoTitle>
      <LocationInfoDesc>{info.description}</LocationInfoDesc>
    </LocationInfo>
  )

  render() {
    const { locationInfo } = this.state;

    return (
      <MapWrap ref={this.mapRef}>
        {locationInfo && this.renderLocationInfo(locationInfo)}
      </MapWrap>
    );
  }
}

Map.propTypes = {
  officeLocation: PropTypes.shape({
    lat: PropTypes.number.isRequired,
    lng: PropTypes.number.isRequired,
  }).isRequired,
};

const mapStateToProps = state => ({
  officeLocation: officeLocationSelector(state),
});

export default connect(mapStateToProps)(Map);
